:root{--white:#fff;--black:#000;--red:#ff4f5e;--light-red:#fdc6c8;--green:#b7d43f;--valid-green:#59cbb7;--near-black:#4a4a4a;--grey:#e7e5e2;--darker-grey:#e6e5e3;--light-grey:#f3f2f0;--lighter-grey:#f9f9f9;--desert-storm:#f3f2f1;--warm-grey:#959595;--blue:#629ff4;--link-color:var(--blue);--override-z-index:3;--top-z-index:2;--middle-z-index:1;--bottom-z-index:0;--base-font-family:"Open Sans",sans-serif;--strong-font-family:"Zilla Slab",Georgia,Utopia,Charter,serif;--font-size:16px;--font-size-xs:12px;--font-size-lg:24px;--font-size-xl:32px;--font-size-xxl:34px;--standard-margin:0.3rem;--wide-desktop-width:1400px;--desktop-width:76rem;--old-desktop-width:55rem;--page-margin:1rem;--input-height:2.5rem;--spinner-height:10rem;--card-shadow:rgba(0,0,0,0.09) 0px 1px 6px,rgba(0,0,0,0.09) 0px 1px 4px;--transition-duration:0.2s;--transition-duration-slow:0.5s;--easing:cubic-bezier(0.4,0,0.2,1);--header-height:4rem;--nav-shadow-overhang:0px 2px 4px rgba(0,0,0,0.03);--dataset-item-margin:0.7rem;--profile-column-margin:30px}@keyframes fade-in{0%{opacity:0;pointer-events:none}to{opacity:1;pointer-events:all}}.md-block{display:none}@media (min-width:768px){.md-block{display:block}}@media (max-width:768px){.hidden-sm-down{display:none!important}}@media (max-width:992px){.hidden-md-down{display:none!important}}@media (min-width:768px){.hidden-md-up{display:none!important}}@media (min-width:992px){.hidden-lg-up{display:none!important}}.language-select{position:relative}.language-select .current{border:1px solid rgba(0,0,0,.1);border-radius:1000px;max-height:50px;min-width:220px;padding:13px 22px;box-sizing:border-box;font-family:Open Sans,sans-serif;font-family:var(--base-font-family);text-transform:uppercase;letter-spacing:.8px;font-weight:600;font-size:14px;background:#fff url(/img/down-arrow.svg) no-repeat 90%;background:var(--white) url(/img/down-arrow.svg) no-repeat 90%;background-size:10px;cursor:pointer}.language-select .current:hover{background-color:#f3f2f1;background-color:var(--desert-storm)}@media (max-width:992px){.language-select .current{margin-top:15px}}.language-select ul{margin:1px 0 0;padding:0;max-height:190px;width:100%;position:absolute;z-index:1;z-index:var(--middle-z-index);background:#fff;background:var(--white);list-style:none;overflow:auto;color:#4a4a4a;color:var(--near-black);box-shadow:0 2px 5px 0 #f3f2f0;box-shadow:0 2px 5px 0 var(--light-grey)}.language-select ul li{border-bottom:1px solid #f3f2f0;border-bottom:1px solid var(--light-grey);cursor:pointer;padding:13px 22px}.language-select ul li:last-child .line{border-bottom:0}.language-select ul li.selected{font-weight:600}.language-select ul li.highlighted{background:#f3f2f0;background:var(--light-grey)}